
.Home {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  // width: 100vh;
  height: 100vh;
    h1 {
      color: var(--yellow)
    }
    *{
      font-size: 3.7vw;
      text-transform: uppercase;
      color:gray;
    }
    ul {
      display: flex;
    }
    .menu {
      display: flex;
      flex-direction: column;
      a {
          text-decoration: none;
          margin-bottom: 1.1em;
          position: relative;
        }
    }
    .small-btn-div {
      display: flex;
    }
  }

  iframe {
    width: 0;
    height: 0;
  }