@keyframes flipAction{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}50%{-webkit-transform:rotateY(180deg);transform:rotateY(180deg)}100%{-webkit-transform:rotateY(360deg);transform:rotateY(360deg)}}@-webkit-keyframes bg-red{50%{-webkit-filter:drop-shadow(0 0 0.75rem #fff);filter:drop-shadow(0 0 0.75rem #fff)}100%{-webkit-filter:drop-shadow(0 0 0.75rem var(--red));filter:drop-shadow(0 0 0.75rem var(--red))}}@keyframes bg-red{50%{-webkit-filter:drop-shadow(0 0 0.75rem #fff);filter:drop-shadow(0 0 0.75rem #fff)}100%{-webkit-filter:drop-shadow(0 0 0.75rem var(--red));filter:drop-shadow(0 0 0.75rem var(--red))}}@-webkit-keyframes bg-yellow{50%{-webkit-filter:drop-shadow(0 0 0.75rem #fff);filter:drop-shadow(0 0 0.75rem #fff)}100%{-webkit-filter:drop-shadow(0 0 0.75rem var(--yellow));filter:drop-shadow(0 0 0.75rem var(--yellow))}}@keyframes bg-yellow{50%{-webkit-filter:drop-shadow(0 0 0.75rem #fff);filter:drop-shadow(0 0 0.75rem #fff)}100%{-webkit-filter:drop-shadow(0 0 0.75rem var(--yellow));filter:drop-shadow(0 0 0.75rem var(--yellow))}}@-webkit-keyframes text{50%{text-shadow:0 0 20px orange, 1px 1px 20px orange, 2px 2px 20px orange, -1px -1px 20px orange, -2px -2px 20px orange}}@keyframes text{50%{text-shadow:0 0 20px orange, 1px 1px 20px orange, 2px 2px 20px orange, -1px -1px 20px orange, -2px -2px 20px orange}}@-webkit-keyframes fade-out{100%{width:50%;height:50%;opacity:0}}@keyframes fade-out{100%{width:50%;height:50%;opacity:0}}@-webkit-keyframes showup{from{opacity:0}to{opacity:1}}@keyframes showup{from{opacity:0}to{opacity:1}}@-webkit-keyframes all-flip-back{100%{-webkit-transform:rotateY(180deg);transform:rotateY(180deg)}}@keyframes all-flip-back{100%{-webkit-transform:rotateY(180deg);transform:rotateY(180deg)}}@-webkit-keyframes larger{0%{color:grey;z-index:999;font-size:1.6em;padding:1.5em 5em}100%{transition:1.2s;margin:0 auto;font-size:12px;padding:1em;margin:0 auto}}@keyframes larger{0%{color:grey;z-index:999;font-size:1.6em;padding:1.5em 5em}100%